.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 100%;
    max-width: 720px;
    background: #1C1C21;
    border: 1px solid rgba(76, 76, 255, 0.2);
    box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 40px;
    transform: translate(-50%, -50%);
    z-index: 100;
    box-sizing: border-box;
}

.modal_close_btn {
    position: absolute;
    top: 63px;
    right: 43px;
    padding: 0;
    margin: 0;
    margin-left: auto;
    width: 18px;
    height: 18px;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.modal_close_btn::before,
.modal_close_btn::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 2px;
    border-radius: 200px;
    background: #F2F2F3;
}

.modal_close_btn::before {
    transform: translate(-50%, -50%) rotate(45deg);
}

.modal_close_btn::after {
    transform: translate(-50%, -50%) rotate(-45deg);
}

@media (max-width: 767px) {
    .modal {
        max-width: calc(100% - 20px);
    }
}

@media (max-width: 575px) {
    .modal {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 8px;
        width: 100%;
        height: 100%;
        max-width: none;
    }

    .modal_close_btn {
        top: 16px;
        right: 8px;
        width: 24px;
        height: 24px;
    }
}